import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllFringeBenefitComponent } from './fringe-benefit/all-fringe-benefit/all-fringe-benefit.component';
import { AllHouseLevyComponent } from './house-levy/all-house-levy/all-house-levy.component';
import { AllNhifComponent } from './nhif/all-nhif/all-nhif.component';
import { AllPayeComponent } from './paye/all-paye/all-paye.component';
import { AllPayeReliefComponent } from './paye-relief/all-paye-relief/all-paye-relief.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';
import { AllNssfComponent } from './nssf/all-nssf/all-nssf.component';

const routes: Routes = [
  {
    path: "fringe-benefit",
    component: AllFringeBenefitComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

  {
    path: "house-levy",
    component: AllHouseLevyComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
  {
    path: "nhif",
    component: AllNhifComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
  {
    path: "nssf",
    component: AllNssfComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
  {
    path: "paye",
    component: AllPayeComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
  {
    path: "paye-relief",
    component: AllPayeReliefComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DeductionsRoutingModule { }
